<template>
    <!--    注意 这个地方的自定命令中的value 是要在单引号内的 代表是一个字符串
            narrow是binding.value,column是binding.arg-->
    <div v-theme:column="'narrow'" id="show-blogs">
        <h1>博客总览</h1>
        <input type="text" v-model="search" placeholder="搜索">
        <div v-for="item in filteredBlogs" class="single-blog" v-bind:key="item.title">
            <router-link v-bind:to="'/blog/'+item.id">
                <h2 v-rainbow>{{item.title | to-uppercase}}</h2>
            </router-link>
            <article>
                {{item.content | snippet}}
            </article>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'show-blogs',
        data() {
            return {
                blogs: [],
                search: ""
            };
        },
        created() {
            // 使用本地的post.json文件
            // this.$http.get('/data/posts.json').then(
            //     function (data) {
            //         this.blogs = data.body.slice(0, 20);
            //     }
            // )
            this.$http.get("https://my-blog-61223.firebaseio.com/post.json")
                .then(function (data) {
                    return data.json();
                }).then(function (data) {
                var blogArray = [];
                for (let key in data) {
                    console.log(key);
                    console.log(data[key]);
                    data[key].id = key;
                    blogArray.push(data[key]);
                }
                console.log(blogArray);
                this.blogs = blogArray;
            })
        },
        //局部注册自定义指令
        directives: {
            'rainbow': {
                bind(el, binding, vNode) {
                    el.style.color = "#" + Math.random().toString(16).slice(2 - 8);
                }
            }
        },
        //局部注册自定义filter
        filters: {
            "to-uppercase": function (value) {
                return value.toUpperCase();
            }
        },
        computed: {
            //vue实例变化的时候会调用 如果search不变化 filteredBlogs的值也不会变化
            filteredBlogs() {
                return this.blogs.filter((blog) => {
                    return blog.title.match(this.search);
                })
            }
        }
    }
</script>

<style>

    #show-blogs {
        max-width: 800px;
        margin: 0 auto;
    }

    .single-blog {
        padding: 20px;
        margin: 20px 0;
        box-sizing: border-box;
        background: #eee;
        border: 1px dotted #aaa;
    }

    #show-blogs a {
        color: #444;
        text-decoration: none;
    }

    input[type="text"] {
        padding: 8px;
        width: 100%;
        box-sizing: border-box;
    }
</style>
