<template>
    <div id="add-blog">
        <h2>添加博客</h2>
        <h2 v-if="submit">您的博客发布成功！</h2>
        <form v-if="!submit">
            <label>博客标题</label>
            <input type="text" v-model="blog.title" required>

            <label>博客内容</label>
            <textarea name="" id="" cols="30" rows="10" v-model="blog.content"></textarea>

            <div id="checkboxes">
                <label>Vue.js</label>
                <input type="checkbox" v-model="blog.categories" value="Vue.js">
                <label>Node.js</label>
                <input type="checkbox" v-model="blog.categories" value="Node.js">
                <label>React.js</label>
                <input type="checkbox" v-model="blog.categories" value="React.js">
                <label>Angular.js</label>
                <input type="checkbox" v-model="blog.categories" value="Angular.js">
            </div>
            <label>作者：</label>
            <select v-model="blog.author">
                <option v-for="author in blog.authors" v-bind:key="author">
                    {{author}}
                </option>
            </select>
            <button v-on:click.prevent="post">添加博客</button>
        </form>

        <hr>

        <div id="preview">
            <h3>博客总览</h3>
            <p>博客标题：{{blog.title}}</p>
            <p>博客内容：</p>
            <p>{{blog.content}}</p>
            <p>博客分类：</p>
            <ul>
                <li v-for="category in blog.categories" v-bind:key="category">
                    {{category}}
                </li>
            </ul>
            <p>作者:{{blog.author}}</p>

        </div>
    </div>
</template>

<script>
    export default {
        name: 'add-blog',
        data() {
            return {
                blog: {
                    title: "",
                    content: "",
                    categories: [],
                    authors: ["", "Ada", "Bucky", "Candy", "Dandi"],
                    author: "",

                },
                submit: false
            }
        },
        methods: {
            post: function () {
                //需要翻墙
                this.$http.post("https://my-blog-61223.firebaseio.com/post.json", this.blog).then(function (data) {
                    this.submit = true;
                    console.log(data)
                })
            }
        },
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    #add-blog * {
        box-sizing: border-box;
    }

    #add-blog {
        margin: 20px auto;
        max-width: 600px;
        padding: 20px;
    }

    label {
        display: block;
        margin: 10px 0 10px;
    }

    input[type=text], textarea, select {
        display: block;
        width: 100%;
        padding: 8px;
    }

    textarea {
        height: 200px;
    }

    #checkboxes label {
        display: inline-block;
        margin-top: 0;
    }

    #checkboxes input {
        display: inline-block;
        margin-right: 10px;
    }

    button {
        display: block;
        margin: 20px 0;
        background: crimson;
        color: #ffffff;
        border: 0;
        padding: 14px;
        border-radius: 4px;
        font-size: 18px;
        cursor: pointer;
    }

    #preview {
        padding: 10px 20px;
        border: 1px dotted #ccc;
        margin: 30px 0;
    }

    h3 {
        margin-top: 10px;
    }
</style>
